.Nav_Block {
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    z-index: 9999;
    background-color: #ffffff;
    margin-left: auto;
    margin-right: auto;
    /* backdrop-filter: blur(10px); */
}
.Nav_BlockCenter {
    position: relative;
    width: 91%;
    top: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 28px 92px;
    z-index: 9999;
    background-color: #ffffff;
    margin-left: auto;
    margin-right: auto;
}
.Nav_Block_right {
    position: relative;
    display: flex;
    justify-content: flex-start;
    width: 25%;
}
.Nav_Block_left {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-left: 30%;
    width: 75%;
}
.Nav_Block_right .Nav_Block_right_a {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}



.Nav_Block_right_a_title {
    padding-left: 14px;
    font-family: 'Poppins-ExtraLight';
    font-size: 16px;
    color: #102876;
}



.a_style_none {
    text-decoration: none;
    color: inherit;
    font-size: 16px;
    font-family: 'Raleway-Light';
    display: flex;
    flex-wrap: nowrap;
    color: #102876;
}