.preloader{
    position: fixed;
    top: 0;
    left: 0;
    background: #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    z-index: 99999;
    color: white;
}
.logo{
    position: relative;
    transition: 0.8s;
    opacity: 1;
    animation: moveAround 2s infinite;

}
@keyframes moveAround {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}
.logo_center{
    width: 300px;
    height: 300px;
}
